<template>
<div class="checkPending">
  <div class="title">
    <slot name="pageTitle"></slot>
  </div>
  <div class="test">
    <button @click="setCount">点击改变全局state的count值</button>{{count}}<br/>
    <button @click="setTeacherCount">点击改变模块teacher下state的count值</button>{{teacherCount}}<br/>
    {{fileterTodos}}
  </div>
</div>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex'
export default {
  name: 'CheckPending',
  computed: {
    ...mapState({
      count: state => state.count,
      teacherCount: state => state.teacher.count
    }),
    ...mapGetters({
      fileterTodos: 'fileterTodos'
    })
  },
  methods: {
    ...mapActions({
      setCount: 'setCount',
      setTeacherCount: 'teacher/setCount'
    })
  }
}
</script>

<style scoped>

</style>



